.original-button {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  margin: 20px auto;
  text-decoration: none;
  color: #9b9b9b;
  font-size: 18px;
  border-radius: 50px;
  width: 71px;
  height: 71px;
  font-weight: bold;
  transition: 0.4s;
  box-shadow: 0px 0px 0px 4px rgba(0, 0, 0, 0.4);
  background-color: #959F97 ;
}

.original-button:hover {
  box-shadow: 0px 2px 10px 6px rgba(0, 0, 0, 1);
  background-color: white ;

}

